@import url(https://unpkg.com/leaflet@1.5.1/dist/leaflet.css);
@import url(https://use.fontawesome.com/releases/v5.0.6/css/all.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#OverviewMap > div {
  height: 300px; }

#RestDescription > div {
  margin-top: 40px; }

#RestDescription img {
  width: 100%; }

#RestDescription h2 {
  color: #553; }

#RestDescription h3 {
  color: #997; }

#RestDescription .rest-about span {
  display: block;
  margin: 10px 0; }

#RestDescription .rest-about .rest-ab-address:before {
  content: "\f3c5";
  margin-right: 0.5em; }

#RestDescription .rest-about .rest-ab-cuisine:before {
  content: "\f0ac";
  margin-right: 0.5em; }

#RestDescription .rest-openinghours .rest-op-day {
  margin-right: 0.5em; }

#RestDescription .rest-openinghours span {
  margin-top: 10px;
  margin-bottom: 10px; }

#RestDescription .rest-review {
  margin-bottom: 20px;
  margin-top: 20px;
  overflow-y: hidden; }
  #RestDescription .rest-review > span {
    display: block;
    margin: 10px 0;
    margin-right: 0.5em;
    float: left;
    color: #666; }
  #RestDescription .rest-review .rest-rv-name:before {
    content: "\f007";
    margin-right: 0.5em; }
  #RestDescription .rest-review .rest-rv-date:before {
    content: "\f073";
    margin-right: 0.5em; }
  #RestDescription .rest-review > p {
    float: left;
    display: block;
    width: 100%;
    margin-top: 0px; }
  #RestDescription .rest-review .rating {
    float: right; }

@media screen and (min-width: 498px) {
  body {
    position: absolute;
    height: 100%;
    width: 100%; }
  #root {
    height: 100%; }
  #RestaurantPage {
    height: 100%;
    overflow: hidden;
    display: grid;
    grid-gap: 0px;
    grid-template-columns: 3fr 2fr;
    margin-right: 0; }
    #RestaurantPage header {
      grid-column: span 2; }
    #RestaurantPage .leaflet-container {
      height: 100%; }
    #RestaurantPage #RestDescription {
      overflow-y: scroll;
      margin: 0;
      box-shadow: 0px 0px 10px 5px #999;
      z-index: 2999;
      padding: 5%;
      width: 90%; } }

#OverviewMap > div {
  height: 500px; }
  #OverviewMap > div .leaflet-popup-pane a, #OverviewMap > div .leaflet-popup-pane a:visited, #OverviewMap > div .leaflet-popup-pane a:hover, #OverviewMap > div .leaflet-popup-pane a:active, #OverviewMap > div .leaflet-popup-pane a:focus {
    color: #997;
    text-decoration: none;
    text-transform: uppercase;
    cursor: pointer;
    -webkit-transition: color 0.2s;
    transition: color 0.2s; }
  #OverviewMap > div .leaflet-popup-pane a:focus, #OverviewMap > div .leaflet-popup-pane a:hover {
    color: #775; }

#MapFilter {
  margin-top: 60px;
  margin-bottom: 60px;
  background-color: #ddd;
  padding: 20px;
  box-sizing: border-box;
  overflow: hidden; }
  #MapFilter label {
    width: 50%;
    float: left; }
    #MapFilter label select {
      margin-left: 1em; }
  @media screen and (max-width: 497px) {
    #MapFilter label {
      width: 100%; } }

#RestaurantPreview {
  margin-top: 60px;
  margin-bottom: 60px;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-gap: 40px; }
  @media screen and (max-width: 767px) {
    #RestaurantPreview {
      grid-template-columns: auto auto; } }
  @media screen and (max-width: 497px) {
    #RestaurantPreview {
      grid-template-columns: auto; } }
  #RestaurantPreview a {
    display: block;
    height: 100%; }
  #RestaurantPreview .restaurant-thumb {
    background-color: #ddd;
    height: 100%;
    text-decoration: none; }
    #RestaurantPreview .restaurant-thumb img {
      width: 100%; }
    #RestaurantPreview .restaurant-thumb .restaurant-content {
      padding: 20px; }
      #RestaurantPreview .restaurant-thumb .restaurant-content h4 {
        text-transform: uppercase; }
      #RestaurantPreview .restaurant-thumb .restaurant-content span {
        display: block; }
      #RestaurantPreview .restaurant-thumb .restaurant-content h4, #RestaurantPreview .restaurant-thumb .restaurant-content span {
        color: #333;
        text-decoration: none; }
  #RestaurantPreview .restaurant-thumb:hover {
    box-shadow: 2px 2px 10px 5px #aa9; }
  #RestaurantPreview .restaurant-thumb:focus {
    background-color: #ccc; }
  #RestaurantPreview .restaurant-thumb:hover, #RestaurantPreview .restaurant-thumb:focus, #RestaurantPreview .restaurant-thumb:active, #RestaurantPreview .restaurant-thumb:visited {
    text-decoration: none; }
    #RestaurantPreview .restaurant-thumb:hover h4, #RestaurantPreview .restaurant-thumb:hover span, #RestaurantPreview .restaurant-thumb:focus h4, #RestaurantPreview .restaurant-thumb:focus span, #RestaurantPreview .restaurant-thumb:active h4, #RestaurantPreview .restaurant-thumb:active span, #RestaurantPreview .restaurant-thumb:visited h4, #RestaurantPreview .restaurant-thumb:visited span {
      color: #333;
      text-decoration: none; }

/* FA CSS import */
.container {
  width: 90%;
  max-width: 900px;
  margin: 0px auto; }

.icon-pseudo:before, .icon-pseudo:after {
  display: inline-block;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
  font-weight: 900; }

header {
  padding: 20px 0;
  padding: 20px 0;
  background-color: #553;
  z-index: 3000; }
  header h1 a, header h1 a:visited, header h1 a:hover, header h1 a:focus, header h1 a:active {
    color: #997;
    text-decoration: none; }
  header h1 a:hover, header h1 a:focus {
    color: #aa8; }

.rating span {
  display: block;
  margin: 10px 0;
  float: left; }

.rating span:before {
  display: inline-block;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  content: "\f005"; }

.rating .sA:before {
  color: #bb9603; }

.rating .sI:before {
  color: #999; }

